body {
  height: 100vh;
  background: radial-gradient(circle, #ffdde1, #ee9ca7);
  justify-content: center;
  align-items: center;
  margin: 0;
  font-family: Arial, sans-serif;
  display: flex;
}

.container {
  text-align: center;
  position: relative;
}

.envelope {
  width: 220px;
  height: 140px;
  background: #ff6b81;
  border-radius: 10px;
  justify-content: center;
  align-items: center;
  transition: transform .5s ease-in-out;
  display: flex;
  position: relative;
  overflow: hidden;
  box-shadow: 0 10px 20px rgba(0, 0, 0, .2);
}

.flap {
  width: 0;
  height: 0;
  z-index: 3;
  border-bottom: 90px solid #ff4757;
  border-left: 110px solid rgba(0, 0, 0, 0);
  border-right: 110px solid rgba(0, 0, 0, 0);
  transition: transform .5s ease-in-out;
  position: absolute;
  top: 0;
  left: 0;
  transform: rotateX(180deg);
  box-shadow: inset 0 -4px 8px rgba(0, 0, 0, .1);
}

.envelope-body {
  width: 100%;
  height: 100%;
  z-index: 1;
  background: linear-gradient(#ff6b81, #ff4757);
  border-radius: 10px;
  position: absolute;
}

.letter {
  width: 150px;
  height: 110px;
  opacity: 0;
  z-index: 2;
  background: #fff;
  border-radius: 5px;
  justify-content: center;
  align-items: flex-start;
  font-size: 14px;
  font-weight: bold;
  transition: opacity .5s, bottom .5s;
  display: flex;
  position: absolute;
  bottom: -100px;
  left: 50%;
  transform: translateX(-50%);
  box-shadow: 0 4px 10px rgba(0, 0, 0, .2);
}

button {
  color: #fff;
  cursor: pointer;
  background: linear-gradient(to right, #ff758c, #ff7eb3);
  border: none;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  margin-top: 20px;
  padding: 10px 20px;
  font-size: 16px;
  transition: background .3s, transform .2s;
  display: flex;
  box-shadow: 0 4px 8px rgba(0, 0, 0, .2);
}

button:hover {
  background: linear-gradient(to right, #ff4b8b, #ff6b81);
  transform: scale(1.05);
}

.heart {
  color: red;
  opacity: 0;
  z-index: 999;
  font-size: 20px;
  animation: floatUp 2s ease-in-out;
  position: absolute;
}

@keyframes floatUp {
  0% {
    opacity: 1;
    transform: translateY(0);
  }

  100% {
    opacity: 0;
    transform: translateY(-100px);
  }
}

